/* src/components/SectionWrapper.css */

.section-wrapper {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding: 4rem 1rem; */
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s ease-out;
}

.section-wrapper.in-view {
  opacity: 1;
  transform: translateY(0);
}

.section-content {
  width: 100%;
  max-width: 1200px; /* WIDER WIDTH */
  margin: 0 auto;
  padding: 0 0rem;
  text-align: center;
}

.dark-bg {
  background-color: #0f172a;
}

.light-bg {
  background-color: #ffffff;
}

.dark-text {
  color: #ffffff;
}

.light-text {
  color: #0f172a;
}
