.background-bloch-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 0;
  overflow: hidden;
  display: block;
}

.background-bloch-container canvas {
  width: 100% !important;
  height: 100% !important;
  display: block;

}

.coordinate-tooltip {
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  color: #000;
  font-size: 0.85rem;
  font-family: monospace;
  padding: 4px 8px;
  border-radius: 6px;
  pointer-events: none;
  transition: opacity 0.2s ease;
  opacity: 0;
  z-index: 5;
}

.coordinate-tooltip.visible {
  opacity: 1;
}
