/* Inherits layout from .section-content via SectionWrapper */

.section-content {
  min-height: 100vh;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Title */
.section-title {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

/* Message text */
.contact-message {
  font-size: 1.125rem;
  color: whitesmoke;
  line-height: 1.6;
  margin-bottom: 2rem;
}

/* Form layout */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-width: 500px;
  margin-bottom: 2.5rem;
}

/* Input & textarea fields */
.contact-form input,
.contact-form textarea {
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 8px;
  background-color: #1e293b;
  color: #fff;
  font-size: 1rem;
}

.contact-form input::placeholder,
.contact-form textarea::placeholder {
  color: #94a3b8;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  box-shadow: 0 0 0 2px #334155;
}

/* Submit button */
.contact-submit {
  background-color: #e94560;
  color: white;
  border: none;
  padding: 0.75rem;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  font-size: 1rem;
  transition:
    background-color 0.3s ease,
    transform 0.3s ease,
    font-size 0.3s ease,
    box-shadow 0.3s ease;
}

.contact-submit:hover {
  background-color: #e94560; /* same, just for consistency */
  color: white;
  font-size: 1.03rem;
  transform: scale(1.02);
  box-shadow: 0 0 2px #e94560, 0 0 6px rgba(233, 69, 96, 0.3);
}

.contact-submit:active {
  transform: scale(1.03);
  box-shadow: 0 0 6px #e94560;
}
/* Social icons */
.social-icons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.social-icons a {
  font-size: 2rem;
  color: white;
  transition: transform 0.2s ease, color 0.2s ease;
}

.social-icons a:hover {
  color: #e94560;
  transform: scale(1.1);
}


.site-credit {
  width: 100%;
  padding: 1rem 0;
  font-size: 0.8rem;
  color: #94a3b8;
  opacity: 0.6;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  background-color: #0f172a; /* match your theme */
}


.site-credit .heart {
  display: inline-block;
  animation: beat 1.2s infinite;
  color: #e94560;
}

@keyframes beat {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.3); }
}

