.navbar {
  position: fixed;
  width: 100%;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 4vw;
  background-color: transparent;
  transition: background-color 0.3s ease, color 0.3s ease;
}

  .navbar.hidden {
    top: -100px; /* moves navbar off-screen */
  }
  
  .navbar.visible {
    top: 0;
  }
  
  .navbar.dark {
    background-color: #0f172a;
    color: white;
  }
  
  .navbar.light {
    background-color: white;
    color: #0f172a;
  }
  
  .nav-left,
  .nav-right {
    display: flex;
    align-items: center;
  }
  
  .nav-left {
    flex: 1;
    justify-content: flex-start;
    gap: 2rem;
  }
  
  .nav-center {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    font-size: 1.8rem;
    font-weight: bold;
    cursor: pointer;
  }
  
  .nav-right {
    flex: 1;
    justify-content: flex-end;
    gap: 1rem;
  }
  
  .desktop-links {
    display: flex;
    gap: 2rem;
  }
  
  button {
    background: none;
    border: none;
    font: inherit;
    cursor: pointer;
    font-weight: 500;
    transition: color 0.3s ease;
  }
  
  .contact-btn {
    border: 1px solid currentColor;
    padding: 0.4rem 1rem;
    border-radius: 999px;
    font-weight: 500;
  }
  
  .hamburger {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
  }
  
  .mobile-menu {
    position: absolute;
    top: 100%;
    left: 0;
    margin-left: 0;
    padding: 1rem 1rem;
    border-radius: 0;
    flex-direction: column;
    gap: 1rem;
    display: flex;
    background-color: inherit;
    width: max-content;
    opacity: 0;
    transform: translateY(-10px);
    animation: slideIn 0.3s ease forwards;
  }
  
  @keyframes slideIn {
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .mobile-menu.dark {
    background-color: #0f172a;
  }
  
  .mobile-menu.light {
    background-color: white;
  }
  
  .mobile-menu .nav-btn.light {
    color: white;
  }
  
  .mobile-menu .nav-btn.dark {
    color: #0f172a;
  }
  
  /* Show only on mobile */
  @media (max-width: 768px) {
    .desktop-links {
      display: none;
    }
  
    .hamburger {
      display: block;
    }
  
    .nav-center {
      text-align: center;
      font-size: 1.5rem;
    }
  
    .mobile-menu {
      display: flex;
    }

    .contact-btn {
      padding: 0.3rem 0.7rem; /* smaller padding */
      font-size: 0.85rem;      /* smaller text */
    }
  }
  

  .nav-btn {
    position: relative;
    color: inherit;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;
    overflow: hidden;
  }
  
  .nav-btn::after {
    content: '';
    position: absolute;
    bottom: -2px;
    right: 0;
    width: 100%;
    height: 2px;
    background-color: #e94560;
    transform: scaleX(0);
    transform-origin: right;
    transition: font-size 0.3s ease,transform 0.4s ease-out;
  }
  
  .nav-btn:hover {
    color: #e94560;
    /* font-size: 1.01rem; */
  }
  
  .nav-btn:hover::after {
    transform: scaleX(1.02);
    transform-origin: left;
  }
  
  .contact-btn {
    border: 1px solid currentColor;
    padding: 0.4rem 1rem;
    border-radius: 999px;
    font-weight: 500;
    background-color: transparent;
    color: inherit;
    transition: 
      background-color 0.3s ease,
      color 0.3s ease,
      border-color 0.3s ease,
      font-size 0.3s ease,
      transform 0.3s ease;
  }
  
  .contact-btn:hover {
    background-color: #e94560;
    /* color: #0f172a; */
    color: white;
    border-color: #e94560;
    font-size: 1.05rem;           
    transform: scale(1.01);
    box-shadow: 0 0 2px #e94560, 0 0 6px rgba(233, 69, 96, 0.2);
  }
