/* src/components/QuantumEquations.css */

.quantum-equations-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    /* z-index: -1; */
    overflow: hidden;
    pointer-events: none;
  }
  
  .equation {
    position: absolute;
    font-size: 1.2rem;
    color: rgba(180, 180, 180, 0.5);  /* original soft gray-white */
    filter: blur(0.8px); 
    font-family: 'Courier New', monospace;
    white-space: nowrap;
    user-select: none;
    transition: transform 0.4s ease;
    pointer-events: none;
  }
  
  