.app-container {
  position: relative;
  background-color: #0f172a;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  text-shadow: 0 0 10px #000;
  font-family: 'Poppins', sans-serif;
  padding: 2rem;
  max-width: 100vw;
}

h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

p {
  font-size: 1.2rem;
  max-width: 600px;
  text-align: center;
  min-height: 50px;
  color: white;
}

.button-group {
  margin-top: 2rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

.btn {
  
  background-color: #e94560;
  /* background-color: #005A9C; */
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 500; 
  font-family: 'Poppins', sans-serif;
  letter-spacing: 0.5px; /* optional for a cleaner, more spaced look */
  text-decoration: none;
  transition: all 0.3s ease;
  box-shadow: none;
  border: 2px solid transparent;
  transform: scale(1);
  display: inline-block;
  text-align: center;
  cursor: pointer;
}

.btn:hover {
  background-color: #ffffff;
  color: #002244;
  border-color: #002244;
  transform: scale(1.05);
  text-decoration: none;

}

a.btn {
  text-decoration: none !important;
  font-size: inherit;
  font-weight: inherit;
  color: inherit;
  text-shadow: none;
}

a.btn:hover {
  text-decoration: none !important;
}



body, .app-container {
  font-family: 'Poppins', sans-serif;
}

.profile-pic {
  width: 170px;
  height: 170px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 1rem;
  background-color: #0b1d3a;
  box-shadow: none;
  transition: transform 0.3s ease;
  z-index: 10;
  position: relative;
}

.profile-pic:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

.animated-heading {
  font-size: 3rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  cursor: default;
}

.letter {
  display: inline-block;
  transition: transform 0.3s ease;
  transform: scale(1);
  position: relative;
  text-shadow: 0 0 10px #000;
  z-index: 1;
}

.letter:hover {
  transform: scale(1.5);
  z-index: 10;
}

.letter:hover + .letter,
.letter:has(+ .letter:hover) {
  transform: scale(1.2);
  z-index: 5;
}

.Typewriter__wrapper {
  color: white !important;
  font-weight: bold;
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
}

.Typewriter__cursor {
  color: white !important;
}

.typewriter-text {
  font-size: 1.2rem;
  color: white;
  text-align: center;
  min-height: 50px;
  z-index: 5;
}

.wormhole-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  background-color: black;
  pointer-events: none;
}

.site-credit-main {
  position: absolute;
  bottom: -1rem; /* slight spacing from the bottom */
  width:100%;

  font-size: 0.8rem;
  color: #94a3b8;
  opacity: 0.6;
  text-align: center;
}

@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }

  p {
    font-size: 1rem;
    padding: 0 1rem;
  }

  .button-group {
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
  }

  .btn {
    width: 100%;
    padding: 0.75rem 1.5rem;  /* ðŸ”§ add padding for inner gap */
    text-align: center;
    display: flex;            /* ðŸ”§ center vertically and horizontally */
    justify-content: center;
    align-items: center;
    height: auto;             /* ðŸ”§ remove fixed height */
    min-height: 10vw;         /* maintain responsiveness */
    box-sizing: border-box;
  }

  animated-heading {
    font-size: 1.5rem;
    flex-wrap: wrap;
    line-height: 1.2;
  }

  .letter {
    font-size: 1.8rem !important;
  }

  .profile-pic {
    width: 100px !important;
    height: 100px !important;
  }

  .typewriter-text {
    font-size: 0.95rem;
  }

  .site-credit-main{
    font-size: 0.8rem !important;
    bottom: -1rem !important;
  }
}