.skills-section-container {
  width: 100vw;
  min-height: 100vh;
  background-color: #ffffff;
  padding: 5rem 2rem 6rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

.skills-title {
  font-size: 2.5rem;
  color: #0f172a;
  margin-bottom: 1rem;
}

.skills-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem;
}

.skills-list li {
  list-style: none;
  background-color: #0f172a !important;
  color: white !important;
  padding: 0.5rem 1.2rem !important;
  border-radius: 999px !important;
  font-size: 0.9rem !important;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.15) !important;
  text-align: center;
  display: inline-block !important;
  border: none !important;
}

.skills-list li:hover {
  transform: scale(1.05);
}

.carousel-wrapper {
  padding: 5rem 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.carousel-container {
  width: 90%;
  overflow: hidden;
  position: relative;
}

.carousel {
  display: flex;
  transition: transform 0.5s ease;
  width: 100%; /* for 2 cards per slide */
}

.slide {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 2rem;
  flex-shrink: 0;
}

.card {
  width: 20vw;
  height: 550px;
  background-color: #0f172a;
  /* color: #e94560; */
  border-radius: 16px;
  overflow: hidden;
  cursor: pointer;
  perspective: 1000px;
  flex-shrink: 0;
  box-sizing: border-box;
}

.card-inner {
  width: 100%;
  height: 100%;
  transition: transform 0.6s ease;
  transform-style: preserve-3d;
  position: relative;
  box-sizing: border-box;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-back {
  background-color: #002244;
  transform: rotateY(180deg);
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  box-sizing: border-box;
}

.card-front {
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  box-sizing: border-box;
}

.card-overlay {
  background: rgba(15, 23, 42, 0.8); /* semi-transparent dark overlay */
  width: 100%;
  height: 100%;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.card-back {
  background-color: rgba(0, 34, 68, 0.9);
  transform: rotateY(180deg);
}

.card-content {
  padding: 1rem;
  text-align: center;
}

.card-title {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #f63858;
  /* text-shadow: 0 0 8px rgba(4, 1, 1, 0.3); */
}

.card-text {
  font-size: 1rem;
  color: #ffffff !important;
  /* text-shadow: 0 0 6px rgba(255, 255, 255, 0.4); */
}

.navg-btn {
  width: 40px;
  height: 40px;
  background-color: #0f172a;
  color: #ffffff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}

.navg-btn.prev {
  left: 1rem;
}

.navg-btn.next {
  right: 1rem;
}

.dots {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
}

.dot {
  width: 10px;
  height: 10px;
  background-color: #0f172a;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0.5;
  transition: opacity 0.3s;
}

.dot.active {
  opacity: 1;
}

@media (min-width: 2200px){
  .card {
    width: 400px !important;
  }

  .navg-btn.prev {
    left: 10rem;
  }
  
  .navg-btn.next {
    right: 10rem;
  }
}

@media (min-width: 768px) and (max-width: 1000px) {
  .card {
    width: 30vw !important;
  }
}


@media (max-width: 767px) {
  .card {
    width: 60% !important;
    height: 80vw;
  }

  .slide {
    flex-direction: column;
    align-items: center;
  }

  .carousel {
    width: 100%;
  }
}
