/* src/components/BlochSphere.css */

.bloch-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 180%;
  height: 180%;
  pointer-events: none;
  /* z-index: -1; */
  display: flex;
  justify-content: center;
  align-items: center;
  transition: filter 0.8s ease;
  
}


.bloch-canvas {
  pointer-events: none;
  filter: blur(0.8px); 
}

.equation-overlay {
  position: absolute;
  top: 10%;
  left: 5%;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1rem;
  color: rgba(100, 100, 100, 0.8);  /* medium grey */
  opacity: 0.8;
  pointer-events: none;
  z-index: 10;
}


.equation-overlay span {
  margin: 4px 0;
  font-family: 'Courier New', Courier, monospace;
}

.direction-info {
  position: absolute;
  bottom: -40px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.9rem;
  color: #ddd;
  opacity: 0.9;
}

.bloch-canvas {
  pointer-events: none;
}
