/* Container and layout remain the same */
.timeline-container {
  background: transparent;
  color: inherit;
  padding: 4rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 0;
}

.timeline-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

/* Subtitles */
.timeline-subtitles {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  font-weight: 600;
  font-size: 1rem;
  margin-bottom: 1.5rem;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.subtitle-left,
.subtitle-right {
  display: flex;
  justify-content: center;
  font-size: 1.5rem;
}

/* Main timeline wrapper */
.timeline-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 0rem;
  z-index: 1;
  min-height: 120vh;
}

/* Background vertical guide */
.timeline-vertical-line {
  position: absolute;
  top: 105px;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 100%; /* stretch to full wrapper height */
  background-color: rgba(255, 255, 255, 0.15);
  z-index: 1;
  pointer-events: none;
}

/* Red progress bar (grows with scroll) */
.timeline-progress {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: 0;
  background: linear-gradient(to bottom, #e94560 98%, #e94560 90%, transparent 100%);
  z-index: 2;
  transition: height 0.3s ease-out;
  pointer-events: none;
}

/* Row and dot styles */
.timeline-row {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
}

.timeline-left,
.timeline-right {
  width: 100%;
  height: auto;
}

.timeline-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 3;
}

.timeline-dot {
  width: 1rem;
  height: 1rem;
  background-color: #e94560;
  border-radius: 50%;
  z-index: 4;
}

/* Card Design */
.timeline-item {
  background: #f8fafc;
  color: #0f172a;
  border-radius: 0.8rem;
  padding: 1rem 1.2rem;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.04);
  z-index: 4;
}

.timeline-content {
  text-align: center;
}

.timeline-content h3 {
  font-size: 1.2rem;
  font-weight: 700;
  margin: 0.2rem 0;
}

.timeline-content h4 {
  font-size: 1rem;
  font-weight: 600;
  margin: 0.2rem 0;
}

.timeline-content span {
  font-size: 0.85rem;
  color: #475569;
}

.timeline-content p {
  font-size: 0.9rem;
  margin-top: 0.5rem;
  color: #334155;
}

@media (max-width: 600px) {
  .timeline-title {
    font-size: 1.7rem;
  }

  .timeline-wrapper {
    padding: 0 0rem;
    width: 90vw;
  }

  .timeline-subtitles {
    font-size: 0.9rem;
  }

  .subtitle-left,
  .subtitle-right {
    font-size: 1.1rem;
  }

  .timeline-item {
    padding: 0.8rem 0.8rem;
  }

  .timeline-content h3 {
    font-size: 1rem !important;
  }

  .timeline-content h4 {
    font-size: 0.8rem !important;
  }

  .timeline-content span {
    font-size: 0.75rem;
  }

  .timeline-content p {
    font-size: 0.75rem;
  }

  .timeline-dot {
    width: 0.8rem;
    height: 0.8rem;
  }

  .timeline-vertical-line, .timeline-progress {
    top: 100px;
  }
}
