.languages-section {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.languages-title {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 3rem;
  z-index: 2;
  transition: color 0.4s ease;
}

.languages-scroll-container {
  display: flex;
  overflow-x: hidden;
  scroll-snap-type: x mandatory;
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
}

.language-tab {
  flex: 0 0 100%;
  width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.language-tab.white {
  background-color: #ffffff;
  color: #0f172a;
}

.language-tab.dark {
  background-color: #0f172a;
  color: #ffffff;
}

.language-name {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.language-native {
  font-size: 2rem;
  color: inherit;
}

.language-level {
  font-size: 1.2rem;
  margin-top: 0.5rem;
  color: #94a3b8; /* gray tone for readability */
}

.dark .language-level {
  color: #e2e8f0; /* brighter for dark background */
}

.dark .language-native {
  color: #e2e8f0; /* brighter for dark background */
}