.about-text-container {
  padding: 0 2rem;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;  /* vertical center */
  align-items: center;
  text-align: center;
  box-sizing: border-box;
}

.about-text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  padding: 0 2rem;
  width: 100%;
  max-width: 1000px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  pointer-events: none; /* let swipe pass through if needed */
}



.about-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  transition: color 0.5s ease;
}

.about-description {
  font-size: 1.15rem;
  line-height: 1.8;
  text-align: justify;
  transition: color 0.5s ease;
  max-width: 800px;
  margin: 0 auto;         /* Keep horizontally centered */
  display: flex;
  flex-direction: column;
  gap: 1rem;              /* Add spacing between <p> tags */
}

.about-description p {
  transition: color 0.5s ease;
}

/* Text Color Classes */
.light {
  color: white;
}

.dark {
  color: #0f172a;
}

.light-text, .light-text p {
  color: white;
}

.dark-text, .dark-text p{
  color: #0f172a;
}

/* Horizontal Scroll Background */
.about-carousel {
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  width: 100%;
  height: 100%;
  scrollbar-width: none;
  -ms-overflow-style: none;
  -webkit-overflow-scrolling: touch; 
  touch-action: auto;               
}

.about-carousel::-webkit-scrollbar {
  display: none; /* Hide scrollbar in Chrome/Safari */
}

.about-slide {
  flex: 0 0 100%;
  width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
}

/* Background Colors */
.white-bg {
  background-color: white;
}

.dark-bg {
  background-color: #0f172a;
}
