 /* .section-wrapper {
    max-width: 1000px;
    margin: 0 auto;
    padding: 4rem 1rem;
    text-align: center;
  } */

/* Section content */
.section-content {
  font-size: 1.05rem;
  line-height: 1.8;
  text-align: justify;
}

/* Responsive */
@media (max-width: 768px) {
  .navbar {
    flex-wrap: wrap;
    gap: 1rem;
  }

  .section {
    margin: 1.5rem 1rem;
    padding: 1.5rem;
  }
}

/* About Section */
.about-section {
  padding: 4rem 2rem;
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
}

html, body {
  scroll-behavior: smooth;
  height: auto;
  overflow-x: hidden;
  overflow-y: auto;
}

@media (max-width: 700px) {
  html, body {
    font-size: 13px !important; /* ðŸ”¥ Force override */
  }

  h1 {
    font-size: 2rem !important;
  }

  h2 {
    font-size: 1.5rem !important;
  }

  h3 {
    font-size: 1rem !important;
  }

  p,
  ul,
  li {
    font-size: 0.9rem !important;
  }

  .skills-list li {
    font-size: 0.8rem !important;
    padding: 0.3rem 0.6rem;
  }

  .section-content {
    font-size: 0.9rem !important;
    line-height: 1.5;
    text-align: left !important;
  }
}

